'use strict';

/**
* Pad a string.
*
* @module @stdlib/string/pad
*
* @example
* var pad = require( '@stdlib/string/pad' );
*
* var str = pad( 'a', 5 );
* // returns 'a    '
*
* str = pad( 'a', 10, {
*     'lpad': 'b'
* });
* // returns 'bbbbbbbbba'
*
* str = pad( 'a', 12, {
*     'rpad': 'b'
* });
* // returns 'abbbbbbbbbbb'
*
* var opts = {
*     'lpad': 'a',
*     'rpad': 'c'
* };
* str = pad( 'b', 10, opts );
* // returns 'aaaabccccc'
*
* opts = {
*     'lpad': 'a',
*     'rpad': 'c',
*     'centerRight': true
* };
* str = pad( 'b', 10, opts );
* // returns 'aaaaabcccc'
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
